.Details {
  display: block;
  position: relative;
}

.Details h2 {
  margin: 20px 0 0 0;
}

.Details h2:first-child {
  margin-top: 0;
}

.Details ul,
.Details li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.Details .details-internal-link {
  border-radius: 2px;
  box-shadow: inset 0 -0.1rem 0 0 #f05656,
    inset 0 -0.25rem 0 0 #FAC7C7;
  color: #444;
  font-weight: 700;
  margin-right: 3px;
  text-decoration: none;
}

.Details .details-internal-link::after {
  color: #f05656;
  content: ' ⤴︎';
  vertical-align: middle;
}

.Details .details-internal-link:hover,
.Details .details-internal-link:active {
  background: #FAC7C7;
}

.Details .details-timeline-item {
  display: flex;
  margin-bottom: 20px;
}

.Details .details-timeline-item-year {
  flex: 0 0 50px;
  padding-right: 10px;
}

.Details .details-timeline-item-content {
  flex: 1 1 auto;
  padding-left: 10px;
}

.Details .details-timeline-item-year h3 {
  margin: 0;
  font-family: 'Sine Mono', monospace;
  font-size: 14px;
  font-weight: 400;
}

.Details .details-timeline-item p {
  margin: 0 0 20px 0;
}

.Details .details-timeline-item-images figure {
  border: 0;
  padding: 0;
  margin: 0;
}

.Details .details-timeline-item-images img {
  display: block;
  height: 125px;
  margin: 0 auto;
  object-fit: contain;
  width: 125px;
}

.Details .details-timeline-item-images.gallery {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.Details .details-timeline-item-images.gallery .details-timeline-item-image {
  background: #eee;
  flex: 0 0 calc(50% - 2px);
  margin: 1px;
  overflow: hidden;
  padding: 10px 5px;
}

.Details .details-timeline-item-images.gallery figure {  
  display: block;
  margin: 0 1px;
}

.Details .details-timeline-item-images.gallery img {
  margin: 0 auto;
  height: 90px;
  width: 100%;
}

.Details .details-timeline-item-images.gallery figcaption {
  font-size: 12px;
  font-style: italic;
  margin:10px 5px;
}

.Details .notables {
  display: flex;
  flex-wrap: wrap;
}

.Details .notable {
  flex: 0 0 50%;
  overflow: hidden;
  text-align: center;
}

.Details .notable figure {
  border: 0;
  margin: 0;
  padding: 0;
}

.Details .notable img {
  display: block;
  height: 150px;
  object-fit: cover;
  width: 100%;
}

@media screen and (min-width:768px) {
  .Details .notable {
    flex: 0 0 25%;
  }

  .Details .notable img {
    height: 200px;
  }
}

@media screen and (min-width:1280px) {
  .Details .notable {
    flex: 0 0 20%;
  }
}

@media screen and (min-width:1460px) {
  .Details .notable {
    flex: 0 0 10%;
  }
}



/* Lightbox */

.details-lightbox {
  background: rgba(0, 0, 0, .9);
  bottom: 0;
  display: block;
  height: auto;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: auto;
  z-index: 999;
}

.details-lightbox-inner {
  display: block;
  height: 100%;
  position: relative;
  width: 100%;
}

.details-lightbox-content {
  background: transparent;
  display: block;
  height: 60vh;
  left: 50%;
  padding: 10px;
  position: absolute;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 90vw;
}

.details-lightbox-content figure {
  border: 0;
  display: block;
  padding: 0;
  margin: 0;
  width: auto;
}

.details-lightbox-content img {
  display: block;
  height: 40vh;
  margin: 0 0 20px 0;
  object-fit: contain;
  object-position: bottom center;
  width: 100%;
}

.details-lightbox-content figcaption {
  color: #fff;
  font-style: italic;
  text-align: center;
}

.details-lightbox-close {
  color: #fff;
  font-family: 'Sine Mono', monospace;
  font-size: 12px;
  position: absolute;
  right: 5vw;
  text-transform: uppercase;
  top: 5vh;
}

