body {
  background: #fff; /*#fef4ea;*/
  color: #444;
  font-family: 'Unna', serif;
  font-size: 16px;
  line-height: 1.5;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4 {
  font-family: 'Unna', serif;
  font-weight: 700;
}

button {
  outline:none;
}

.Timeline-wrapper {
  display: block;
  height: auto;
  overflow-x: scroll;
  overflow-y: visible;
  width: 100%;
}