.Timeline {
  display: block;
  height: auto;
  margin: 0 auto;
  position: relative;
  width: 320px;
}

@media screen and (min-width: 768px) {
  .Timeline {
    width: 748px;
  }
}

@media screen and (min-width: 1024px) {
  .Timeline {
    width: 1004px;
  }
}

@media screen and (min-width: 1280px) {
  .Timeline {
    width: 1260px;
  }
}

@media screen and (min-width: 1460px) {
  .Timeline {
    width: 1440px;
  }
}

@media screen and (min-width: 1800px) {
  .Timeline {
    width: 1780px;
  }
}

.timeline-chart-container {
  position: relative;
}

/* Epochs */
.Timeline .timeline-chart .epochs-background,
.Timeline .timeline-chart .epochs-labels {
  opacity: 0;
}

@media screen and (min-width: 768px) {
  .Timeline .timeline-chart .epochs-background,
  .Timeline .timeline-chart .epochs-labels {
    opacity: 1;
  }
}

/* Lines */ 

/* Ghosts */
.Timeline .timeline-chart .arc-ghost {
  pointer-events: none;
}

.Timeline .timeline-chart .line-label-ghost {
  display: none;
}

/* Connectors */

/* Legend */
.Timeline .timeline-chart .legend {
  display: none;
}

@media screen and (min-width: 768px) {
  .Timeline .timeline-chart .legend {
    display: block;
  }
}

/* Logo */

.Timeline .logo {
  border-radius: 50%;
  object-fit: contain;
  position: absolute;
  transform: translate3d(-50%, -50%, 0);
}

.Timeline .year {
  color: #444;
  font-family: 'Sine Mono', monospace;
  font-size: 10px;
  line-height: 20px;
  padding: 0 5px;
  position: absolute;
  text-align: center;
  text-shadow: 2px 0 0 #fff,
    0 2px 0 #fff,
    -2px 0 0 #fff,
    0 -2px 0 #fff,
    2px -2px 0 #fff,
    -2px 2px 0 #fff,
    -2px -2px #fff,
    2px 2px #fff;
  transform: translate3d(-50%, -50%, 0);
  width: auto;
}

@media screen and (min-width: 768px) {
  .Timeline .year {
    font-size: 10px;
  }
}

@media screen and (min-width: 1024px) {
  .Timeline .year {
    font-size: 12px;
  }
}

@media screen and (min-width: 1280px) {
  .Timeline .year {
    font-size: 12px;
  }
}