.Intro {
    display: block;
    margin: 5% auto 0 auto;
    max-width: 1200px;
    width: 100%;
}

.Intro .Image {
    background: transparent url(./assets/intro.png) top center no-repeat;
    background-size: cover;
    display: block;
    height: auto;
    margin: 0;
    padding: 0; 
    position: relative;
    width: 100%;
}

.Intro .Image:before {
    content: '';
    display: block;
    height: 0;
    padding-bottom: 57%;
    width: 100%;
}

.Intro .Image .Title {
    background: transparent url(./assets/asinistra.png) bottom center no-repeat;
    background-size: 100% auto;
    bottom: 0;
    height: auto;
    left: 50%;
    margin: 0;
    overflow: hidden;
    padding: 0;
    position: absolute;
    text-indent: -500%;
    top: 50%;
    transform: translate3d(-50%,0,0);
    width: 74%;
}

.Intro .Text {
    display: block;
    padding: 100px 15%;
}

.Intro .Text h2 {
    color: #f08371;
    font-size: 24px;
    margin: 48px 0 0 0;    
}

.Intro .Text h2:first-child {
    margin-top: 0;
}